import React from 'react';
import './App.css';
import OurService from './component/OurService';

import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import Home from './component/Home';
import BookAppointment from './component/BookAppointment';
import AppoitmentConfirmation from './component/AppoitmentConfirmation';
import Doctor from './component/Doctor';
import ListOfDoctors from './component/ListOfDoctors';
import BookAppointmentFrom from './component/BookAppointmentFrom';

function App() {
  return (
    <>
      <div className="App">
      <Router>
        <div className="">
              <Routes>
                <Route path='/' Component={Home} exact />
                <Route path='/service' Component={OurService} exact />
                <Route path='/book-appointment' Component={BookAppointment} exact />
                <Route path='/success-appointment' Component={AppoitmentConfirmation} exact />
                <Route path='/doctor' Component={Doctor} exact />
                <Route path='/listofdoctor' Component={ListOfDoctors} exact />
                <Route path='/appoitment-from' Component={BookAppointmentFrom} exact />
              </Routes>
            </div>
        </Router>
      </div>
    </>
  );
}

export default App;
