import React from 'react'
import Doctors from './DoctorCard'
import "react-multi-carousel/lib/styles.css";
import Carousel from 'react-multi-carousel';

export default function AllDoctors() {
   
const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 4000, min: 3000 },
    items: 10
  },
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 7
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 5
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 2
  }
};

    

return (
    <div>
        <div className="container"style={{marginTop:'5vh'}}>
        <h1 className='py-5'>|| Meet our Doctors ||</h1>
            
            <Carousel responsive={responsive}>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
                <div><Doctors/></div>
            </Carousel>
        </div>
    </div>
)
}
