import React from 'react'
import doctor2 from '../img/doctor2.webp';
// import doctor3 from '../img/doctor3.webp';
import {
  Link
} from "react-router-dom";

export default function Doctors(props) {


  return (
    <div>
      <Link to='/doctor' style={{textDecoration: 'none'}}>
        <div className="container" >
          <div className="card">
            <img src={doctor2} className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className="card-title">Dr. S Sarma</h5>
              <p className="card-text">CHILD SPECIALIST</p>
              <p className="card-text">Kolkata</p>
              <Link to='/doctor' className="btn btn-primary" style={{ fontSize: '0.9rem' }}>Book Now</Link>
            </div>
          </div>
        </div>
      </Link>
    </div>
  )
}
