import React from 'react'
import CardService from './CardService'
import doctor from '../img/doctor1.avif';
import medicines from '../img/Medicines.avif';
import lab from '../img/lab.webp';
import clinic from '../img/Clinic.webp';
import SearchMedicinesStoreByLocation from './SearchMedicinesStoreByLocation';
import BookAppointment from './BookAppointment';
export default function AllServices() {
    return (
        <div>
            <div className="container">
                <h1 className='py-5'>|| Our Services ||</h1>
                <div className="container" style={{ display: 'flex', justifyContent: 'center' }}>
                    <CardService img={doctor} title="Visit a Doctor" text="Search the best doctors, specialities, clinic & hospital nearest to you." btnText="Book Your Appointment" component={BookAppointment} />
                    <CardService img={medicines} title="Medicines" text="Search the best doctors, specialities, clinic & hospital nearest to you." btnText="Order Your Medicines" component={SearchMedicinesStoreByLocation} />
                    <CardService img={lab} title="Lab Tests" text="Search the best doctors, specialities, clinic & hospital nearest to you." btnText="Book Your Lab Test" />
                    <CardService img={clinic} title="Clinic" text="Search the best doctors, specialities, clinic & hospital nearest to you." btnText="Visit Our Clinic" />
                </div>
            </div>
        </div>
    )
}
