import React from 'react'
import logo from '../img/logo1.jpg';
import {
    Link
} from "react-router-dom";


export default function Header() {
    return (
        <div className='Header' >
            <nav className="navbar navbar-expand-lg" style={{ backgroundColor: "rgb(7 219 193)", height: "13vh" }}>
                <div className="container-fluid" style={{ justifyContent: 'start' }}>
                    <Link className="navbar-brand" to='/'>
                        <img src={logo} alt="Logo" width="50vw" height="50vh" className="d-inline-block align-text-top" style={{ borderRadius: "50%" }} />
                    </Link>
                    <h4 className='text-light' style={{ marginRight: "5vw" }}>Healthhepta</h4>
                    <div className="dropdown  me-2 " >
                        <button className="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false" style={{ width: "20vw", color: "black", backgroundColor: "white" }}>
                            Choose Location...
                        </button>
                        <ul className="dropdown-menu">
                            <li><Link className="dropdown-item" href="#">Kolkata</Link></li>
                            <li><Link className="dropdown-item" href="#">Baharampur</Link></li>
                            <li><Link className="dropdown-item" href="#">Other City</Link></li>
                        </ul>
                    </div>
                    <div className="search mx-5 me-2" style={{ display: 'flex', justifyContent: 'start' }}>
                        <input className="form-control" type="search" placeholder="Search Doctors, Clinics, Hospitals, Diseases Etc" aria-label="Search" style={{ width: "35vw", borderTopLeftRadius: '6px', borderTopRightRadius: '0px', borderBottomLeftRadius: '6px', borderBottomRightRadius: '0px' }} />
                        <Link to='/listofdoctor'>
                            <button type="button" className="btn" style={{ backgroundColor: '#febd69', color: 'black', borderTopLeftRadius: '0px', borderTopRightRadius: '6px', borderBottomLeftRadius: '0px', borderBottomRightRadius: '6px' }}>

                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-search" viewBox="0 0 16 16">
                                    <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"></path>
                                </svg>
                            </button>
                        </Link>
                    </div>
                    <div className="cart " style={{ display: 'flex', justifyContent: 'start', marginLeft: "auto" }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="5vw" height="5vh" fill="currentColor" className="bi bi-cart" viewBox="0 0 16 16" >
                            <path d="M0 1.5A.5.5 0 0 1 .5 1H2a.5.5 0 0 1 .485.379L2.89 3H14.5a.5.5 0 0 1 .491.592l-1.5 8A.5.5 0 0 1 13 12H4a.5.5 0 0 1-.491-.408L2.01 3.607 1.61 2H.5a.5.5 0 0 1-.5-.5zM3.102 4l1.313 7h8.17l1.313-7H3.102zM5 12a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm7 0a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm-7 1a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm7 0a1 1 0 1 1 0 2 1 1 0 0 1 0-2z" />
                        </svg>
                    </div>
                    <div className="buttom mx-3">
                        <button type="button" className="btn btn-primary">Login/Signup</button>
                    </div>

                </div>
            </nav>
        </div>
    )
}
