import React from 'react'
import doctor3 from '../img/eyedoctor.webp';
import BookAppointment from './BookAppointment';
import {
  Link
} from "react-router-dom";
import Modal from 'react-modal';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};

export default function SpecialitiesDoctorsCard() {
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    document.body.style.overflow = 'hidden';
}

function closeModal() {
    document.body.style.overflow = 'unset';
    setIsOpen(false);
}
  return (
    <div>
      <Link style={{ textDecoration: 'none' }}>
        <div className="container" >
          <div className="card">
            <img src={doctor3} className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className="card-title">Eye Specialist</h5>
              <p className="card-text">Kolkata</p>
              <Link className="btn btn-primary" style={{ fontSize: '0.9rem' }}  onClick={openModal}>View Doctor's</Link>
              <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
              >
                <BookAppointment closeTheModal={closeModal} />
              </Modal>
            </div>
          </div>
        </div>
      </Link>
    </div>
  )
}
